@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:400,600,700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font-family:'Nunito', sans-serif;vertical-align:baseline;font-family:'Open Sans', sans-serif}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}p{line-height:1.4}#root{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:black}

:root{--black: black;--white: white;--orange: #FFBD70}.appear{-webkit-animation-name:appear;animation-name:appear;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-iteration-count:initial;animation-iteration-count:initial}@-webkit-keyframes appear{0%{opacity:0;transform:translateY(10px)}100%{opacity:1;transform:translateY(0)}}@keyframes appear{0%{opacity:0;transform:translateY(10px)}100%{opacity:1;transform:translateY(0)}}.loader{border:10px solid #f3f3f3;border-top:10px solid #3498db;border-radius:50%;width:50px;height:50px;-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite;margin-bottom:50px}@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.positive{border:solid 3px #1aad06}.negative{border:solid 3px #ee0c0c}.neutro{border:solid 3px #eedf0c}.Countries{max-width:1550px;width:97vw;height:calc( 100vh - 16px)}.Countries__loading{width:100vw;height:calc( 100vh - 16px);display:flex;flex-direction:column;justify-content:center;align-items:center}.Morning,.Noon,.AfterNoon,.Night{width:100%;height:100%;background-color:black;position:relative}.Morning__background,.Noon__background,.AfterNoon__background,.Night__background{width:100%;height:100%;background-size:contain;background-repeat:no-repeat;background-position:center}.Datacard__message{display:none}@media only screen and (max-width: 900px){.Morning__background,.Noon__background,.AfterNoon__background,.Night__background{display:none}.CountryCard .Datacard{top:auto;right:auto}.Datacard__message{display:flex;color:white;color:var(--white);text-align:center;font-weight:normal;line-height:1.2}}.CountryCard{display:flex;justify-content:center;align-items:center;position:absolute;top:0;width:100%;height:100%}

.Home{width:100vw;height:calc(100vh - 16px);display:flex;flex-direction:column;justify-content:center;align-items:center}.Home__content{background:rgba(0,0,0,0.7);width:100%;height:calc(100vh - 16px);position:absolute;padding:50px;box-sizing:border-box;top:0;z-index:10;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.Home__content__body{color:var(--white);text-align:center;display:flex;flex-direction:column;justify-content:center;align-items:center}.Home__content__body__title{font-size:40px;margin-bottom:15px}.Home__content__body__text{max-width:700px;margin:50px 0}.Home__content__body__btns{display:flex;flex-direction:row}.Home__content__body__btns__btn{display:flex;justify-content:center;align-items:center;width:250px;height:40px;background-color:var(--white);color:var(--black);text-decoration:none;font-size:20px;font-weight:bolder;border-radius:5px;margin:0 20px}

.Logo{display:block;width:-webkit-max-content;width:-moz-max-content;width:max-content;border-bottom:solid 5px var(--white);text-decoration:none;padding-bottom:10px;box-sizing:border-box}.Logo h1{font-weight:bolder;color:var(--white);font-size:40px}

.Datacard{width:250px;position:absolute;z-index:2;right:40px;top:40px}.Datacard__body{background-color:var(--white);min-height:380px;padding:30px;box-sizing:border-box;border-radius:11px;box-shadow:0px 0px 4px rgba(105,105,105,0.25);display:flex;flex-direction:column;justify-content:space-between;align-items:center}.Datacard__body__flag{width:90px;height:90px;border-radius:90px;border:solid 5px var(--white);background-size:cover;background-repeat:no-repeat;background-position:center;box-shadow:0px 0px 4px rgba(105,105,105,0.25)}.Datacard__body__state{width:100%;text-align:center;border-radius:11px;box-shadow:0px 0px 4px rgba(105,105,105,0.25);overflow:hidden;margin:20px 0}.Datacard__body__state__header{padding:10px;box-sizing:border-box}.Datacard__body__state__body{padding:15px 10px;box-sizing:border-box;font-size:14px}.Datacard__body__hour{width:100%;display:flex;justify-content:space-around;align-items:center}.Datacard__body__hour__text{text-align:center}.Datacard__btn{display:block;background-color:var(--white);box-shadow:0px 0px 4px rgba(105,105,105,0.25);border-radius:11px;color:var(--black);font-weight:bolder;font-size:14px;text-align:center;text-decoration:none;text-transform:uppercase;margin:15px 0;padding:10px 20px;box-sizing:border-box;transition:all .4s}.Datacard__btn:hover{background-color:#d3d3d3}

.Credits{background:var(--black);display:flex;justify-content:center;align-items:center;color:var(--white);font-size:12px}.Credits a{margin-left:5px;color:var(--white)}

.Huella{width:100vw;height:calc( 100vh - 16px);background-color:var(--white);background-size:cover;background-position:center;display:flex;flex-direction:column;justify-content:space-between;align-items:center;padding-bottom:75px;box-sizing:border-box}.Huella__body{width:100%}.Huella__btns{font-weight:bolder}.Huella__btns__item{margin:0 20px;border:solid 2px var(--orange);cursor:pointer;padding:10px 25px;border-radius:8px;transition:all .4s;text-transform:uppercase;font-weight:bolder;letter-spacing:.4px;color:rgba(0,0,0,0.7)}.Huella__btns__item:hover{color:rgba(0,0,0,0.9)}.Huella__btns button:first-child{background:none}.Huella__btns button:first-child:hover{background:var(--orange)}.Huella__btns button:last-child{background:var(--orange)}.step{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.step__header{margin-bottom:50px;text-align:center}.step__header__title{font-size:30px;margin-bottom:10px}.step__header__question{opacity:.5}.step__body{display:grid;grid-template-columns:repeat(2, 500px);grid-gap:100px 20px}.step__body__item{max-width:500px;width:100%;margin:auto;text-align:center}.step__body__item__subtitle{opacity:.7}.step__body__item__range{width:100%;margin:10px 0}.step__body__item__radius,.step__body__item__checkbox{display:flex;flex-direction:row;justify-content:center;align-items:center;margin-top:20px}.step__body__item__radius div,.step__body__item__checkbox div{margin:0 15px}.step__body__item__radius div label,.step__body__item__checkbox div label{margin-left:5px}.step__body__item__checkbox{flex-direction:column;justify-content:flex-start;align-items:flex-start}.step__body__item__checkbox div{margin:5px 0}.step__body__item-disabled{opacity:.4}.step__form{width:100%;max-width:500px}.step__form__item{width:100%}.step__form__item__title{text-align:center;margin-bottom:50px}.step__form__item div{display:flex;justify-content:space-between;align-items:center;margin-bottom:15px}.step__form__item div input{width:10%;max-width:50px;border:none;background:none;border-bottom:solid 2px #c5c5c5;text-align:center;padding-left:10px;padding-bottom:5px}.step__checkbox{width:100%;max-width:500px}.step__checkbox .step__body__item{width:100%}.step__checkbox .step__body__item__title{text-align:center;margin-bottom:50px}.step__checkbox .step__body__item__checkbox{flex-direction:column;justify-content:flex-start;align-items:flex-start}.step__checkbox .step__body__item__checkbox div{margin:5px 0}.results{height:calc( 100vh - 250px);display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center}.results__title{margin-bottom:40px;font-size:30px}.results__planeta{background:var(--orange);padding:30px;box-sizing:border-box;border-radius:20px;display:flex;flex-direction:column;justify-content:center;align-items:center;margin-bottom:20px}.results__planeta p{margin-bottom:10px}.results__planeta h1{font-size:40px}.results__consumo{margin-bottom:20px}.results__consumo h1{margin:10px 0}.results__consumo__subtitle{opacity:.7}.results__futbol h1{margin-top:20px}.huella__btns__final{display:flex;flex-direction:column;justify-content:center;align-items:center}.huella__btns__final__item{margin:0 20px;background:var(--orange) !important;border:solid 2px var(--orange);cursor:pointer;padding:10px 25px;border-radius:8px;transition:all .4s;text-transform:uppercase;font-weight:bolder;letter-spacing:.4px;color:rgba(0,0,0,0.7);margin-bottom:10px}.huella__btns__final__item:hover{color:rgba(0,0,0,0.9)}.huella__btns__final a{text-decoration:none;color:var(--black);opacity:.7;transition:all .4s}.huella__btns__final a:hover{opacity:1}

.numeroPreguntas{width:100%}.numeroPreguntas__progress{max-width:100%;background:#DCDFE1}.numeroPreguntas__progress .progress{max-width:100%;background-color:var(--orange);height:10px;transition:all .4s}

